/* normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
body {
  color: rgba(0,0,0,0.87);
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
div.hero {
  background: #fff;
  padding-top: 35px;
  padding-bottom: 35px;
}
div.hero header {
  *zoom: 1;
  max-width: 928px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 25px;
  text-align: center;
}
div.hero header:before {
  content: '';
  display: table;
}
div.hero header:after {
  content: '';
  display: table;
  clear: both;
}
div.hero a.logo {
  display: inline-block;
}
div.hero a.logo img {
  width: 76px;
}
div.hero section.intro {
  text-align: center;
}
div.hero section.intro h1 {
  padding-top: 10px;
  font-size: 3em;
  font-weight: 200;
  margin: 0;
}
div.hero section.intro h3 {
  color: rgba(0,0,0,0.54);
  font-size: 1.5em;
  font-weight: 100;
  padding-top: 7px;
  margin: 0;
}
div.hero section.intro button.download {
  display: inline-block;
  margin-top: 30px;
  text-decoration: none;
  padding: 8px 30px;
  color: #fff;
  border-radius: 3px;
  background: -webkit-linear-gradient(top, #ff5722 0%, #f63b00 100%);
  background: linear-gradient(to bottom, #ff5722 0%, #f63b00 100%);
  border: 1px solid #d93400;
  text-shadow: 1px 1px 1px #e73700;
  text-align: center;
  position: relative;
  cursor: pointer;
}
div.hero section.intro button.download:hover {
  background: -webkit-linear-gradient(top, #ff7043 0%, #f63b00 100%);
  background: linear-gradient(to bottom, #ff7043 0%, #f63b00 100%);
}
div.hero section.intro button.download:focus {
  outline: 0;
}
div.hero section.intro button.download.loading {
  color: transparent;
  text-shadow: none;
  pointer-events: none;
}
div.hero section.intro button.download.loading:after {
  -webkit-animation: spin-around 400ms infinite linear;
          animation: spin-around 400ms infinite linear;
  border: 2px solid #fff;
  border-radius: 50%;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 16px;
  width: 16px;
  left: 50%;
  margin-left: -10px;
  margin-top: -10px;
  top: 50%;
  position: absolute;
}
div.hero section.intro p.other-versions {
  font-size: 0.65em;
  margin-top: 6px;
  margin-bottom: 0;
}
div.hero section.intro p.other-versions span {
  color: rgba(0,0,0,0.38);
}
div.hero section.intro p.other-versions a {
  color: rgba(0,0,0,0.54);
}
@-webkit-keyframes spin-around {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes spin-around {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
